import { useState } from "react";
import { motion } from "framer-motion";
import {
  ChevronLeft,
  Dumbbell,
  Heart,
  PlusCircle,
  Package,
} from "lucide-react";
import { Link } from "react-router-dom";
import { LazyLoadImage } from "react-lazy-load-image-component";

// Enhanced categories data in Farsi with SEO-friendly descriptions
const categories = [
  {
    id: 1,
    title: "ایستگاه‌های بدنسازی",
    icon: Dumbbell,
    description:
      "مجموعه‌ای کامل از ایستگاه‌های بدنسازی حرفه‌ای برای تمرینات قدرتی و تناسب اندام، مناسب برای باشگاه‌های مجهز و مدرن",
    color: "blue",
    image: "https://allmaxsport.com/Media/images/gym-equipments.avif",
    alt: "ایستگاه‌های بدنسازی حرفه‌ای برای باشگاه",
    slug: "بدنسازی",
  },
  {
    id: 2,
    title: "دستگاه‌های هوازی",
    icon: Heart,
    description: "تجهیزات پیشرفته برای تمرینات قلبی-عروقی و چربی‌سوزی موثر",
    color: "red",
    image: "https://allmaxsport.com/Media/images/cardio.avif",
    alt: "دستگاه‌های هوازی حرفه‌ای برای باشگاه",
    slug: "هوازی",
  },
  {
    id: 3,
    title: "لوازم جانبی",
    icon: PlusCircle,
    description: "تجهیزات تکمیلی ضروری برای هر باشگاه مدرن با تنوع گسترده",
    color: "green",
    image: "https://allmaxsport.com/Media/images/gym-accessories.avif",
    alt: "لوازم جانبی بدنسازی برای تجهیز باشگاه",
    slug: "لوازم جانبی",
  },
  {
    id: 4,
    title: "دستگاه‌های وزن آزاد",
    icon: Package,
    description:
      "دستگاه‌های وزن آزاد حرفه‌ای مانند اسمیت ماشین و تجهیزات صفحه‌ای برای تمرینات قدرتی پیشرفته و ایمن",
    color: "purple",
    image: "https://allmaxsport.com/Media/images/freeweight-machines.avif",
    alt: "دستگاه‌های وزن آزاد بدنسازی حرفه‌ای",
    slug: "وزن آزاد",
  },
];

const getColorClasses = (color) => {
  const colorMap = {
    blue: {
      bg: "bg-blue-500",
      bgLight: "bg-blue-100",
      text: "text-blue-600",
      border: "border-blue-300",
      gradient: "from-blue-500 to-blue-700",
      shadow: "shadow-blue-200",
    },
    red: {
      bg: "bg-red-500",
      bgLight: "bg-red-100",
      text: "text-red-600",
      border: "border-red-300",
      gradient: "from-red-500 to-red-700",
      shadow: "shadow-red-200",
    },
    green: {
      bg: "bg-green-500",
      bgLight: "bg-green-100",
      text: "text-green-600",
      border: "border-green-300",
      gradient: "from-green-500 to-green-700",
      shadow: "shadow-green-200",
    },
    purple: {
      bg: "bg-purple-500",
      bgLight: "bg-purple-100",
      text: "text-purple-600",
      border: "border-purple-300",
      gradient: "from-purple-500 to-purple-700",
      shadow: "shadow-purple-200",
    },
  };

  return colorMap[color] || colorMap.blue;
};

// Animation variants - simplified for better performance on mobile
const containerVariants = {
  hidden: { opacity: 0 },
  visible: {
    opacity: 1,
    transition: {
      staggerChildren: 0.1,
      delayChildren: 0.1,
    },
  },
};

const itemVariants = {
  hidden: {
    y: 10,
    opacity: 0,
  },
  visible: {
    y: 0,
    opacity: 1,
    transition: {
      type: "spring",
      stiffness: 100,
      damping: 12,
    },
  },
  hover: {
    y: -3,
    transition: {
      type: "spring",
      stiffness: 400,
      damping: 10,
    },
  },
};

export default function CategorySection() {
  const [activeCategory, setActiveCategory] = useState(null);

  return (
    <section
      className="bg-white py-6 px-3 sm:py-8 sm:px-6 lg:px-8"
      dir="rtl"
      aria-labelledby="categories-heading"
    >
      <motion.div
        className="max-w-6xl mx-auto"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 0.5 }}
      >
        <div className="text-center mb-4 sm:mb-8">
          <motion.h2
            id="categories-heading"
            className="text-xl sm:text-2xl md:text-3xl font-bold text-gray-900 inline-block relative"
            initial={{ opacity: 0, y: -10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.2, duration: 0.3 }}
          >
            دسته‌بندی تجهیزات
            <motion.div
              className="absolute -bottom-1 left-0 right-0 h-1 bg-blue-500 rounded-full"
              initial={{ width: "0%" }}
              animate={{ width: "100%" }}
              transition={{ duration: 0.5, delay: 0.3 }}
            ></motion.div>
          </motion.h2>
          <motion.p
            className="mt-2 sm:mt-3 text-sm sm:text-base text-gray-600"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ delay: 0.3 }}
          >
            برترین تجهیزات حرفه‌ای برای باشگاه شما
          </motion.p>
        </div>

        <motion.div
          className="grid grid-cols-2 md:grid-cols-4 gap-2 sm:gap-4 lg:gap-6"
          variants={containerVariants}
          initial="hidden"
          animate="visible"
        >
          {categories.map((category) => {
            const colorClasses = getColorClasses(category.color);

            return (
              <Link
                to={`/products?category=${category.slug}`}
                key={category.id}
                aria-label={`مشاهده دسته ${category.title}`}
                className="block h-full"
              >
                <motion.div
                  variants={itemVariants}
                  whileHover="hover"
                  className="relative overflow-hidden rounded-lg shadow-sm h-full cursor-pointer group"
                  onMouseEnter={() => setActiveCategory(category.id)}
                  onMouseLeave={() => setActiveCategory(null)}
                >
                  <div className="aspect-[4/3] sm:aspect-[3/2] overflow-hidden">
                    <LazyLoadImage
                      src={category.image}
                      alt={category.alt}
                      loading="lazy"
                      className="w-full h-full object-cover transition-transform duration-500 group-hover:scale-105"
                    />
                    <div
                      className={`absolute inset-0 bg-gradient-to-t from-black/80 via-black/40 to-transparent`}
                    ></div>
                  </div>

                  <motion.div
                    className="absolute inset-x-0 bottom-0 p-2 sm:p-3 text-white"
                    initial={{ y: 3, opacity: 0.9 }}
                    whileHover={{ y: 0, opacity: 1 }}
                  >
                    <div className="flex items-center gap-1.5 mb-1">
                      <div
                        className={`p-1.5 rounded-full ${colorClasses.bg} bg-opacity-90`}
                      >
                        <category.icon size={14} className="sm:hidden" />
                        <category.icon size={16} className="hidden sm:block" />
                      </div>
                      <h3 className="text-sm sm:text-base font-bold truncate">
                        {category.title}
                      </h3>
                    </div>

                    <motion.div
                      className="hidden sm:block"
                      initial={{ height: 0, opacity: 0 }}
                      animate={
                        activeCategory === category.id
                          ? { height: "auto", opacity: 1 }
                          : { height: 0, opacity: 0 }
                      }
                      transition={{ duration: 0.3 }}
                    >
                      <p className="text-white/90 text-xs line-clamp-2 mb-2">
                        {category.description}
                      </p>
                    </motion.div>

                    <motion.span
                      className={`mt-1 px-2 sm:px-3 py-1 ${colorClasses.bg} rounded-md flex items-center gap-1 text-white text-xs font-medium w-fit`}
                      whileHover={{ scale: 1.03 }}
                      whileTap={{ scale: 0.97 }}
                    >
                      مشاهده
                      <ChevronLeft size={12} className="sm:hidden" />
                      <ChevronLeft size={14} className="hidden sm:block" />
                    </motion.span>
                  </motion.div>
                </motion.div>
              </Link>
            );
          })}
        </motion.div>

        <motion.div
          className="text-center mt-6 sm:mt-8"
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.4 }}
        >
          <Link to="/products">
            <motion.button
              className="px-4 sm:px-6 py-2 sm:py-2.5 bg-blue-600 text-white rounded-lg shadow-md hover:bg-blue-700 transition-all duration-300 font-medium text-xs sm:text-sm"
              whileHover={{
                scale: 1.03,
                boxShadow: "0 6px 10px -3px rgba(0, 0, 0, 0.1)",
              }}
              whileTap={{ scale: 0.98 }}
              aria-label="مشاهده همه محصولات"
            >
              مشاهده همه محصولات
            </motion.button>
          </Link>
        </motion.div>
      </motion.div>
    </section>
  );
}
